import React from 'react';
import { Link } from 'react-router-dom';

const NavGoL = () => {
    return (
        <nav className="main-nav">
            <ul className="nav-items">
                <li className="nav-item">
                    <Link
                        to="/portfolio"
                        style={{ textDecoration: 'none', color: '#ccc' }}
                    >
                        About
                    </Link>
                </li>
                <li className="nav-item">
                    <Link
                        to="/portfolio"
                        style={{ textDecoration: 'none', color: '#ccc' }}
                    >
                        Project
                    </Link>
                </li>
                <li className="nav-item">
                    <Link
                        to="/portfolio"
                        style={{ textDecoration: 'none', color: '#ccc' }}
                    >
                        Contact
                    </Link>
                </li>
            </ul>
        </nav>
    );
};

export default NavGoL;
