import React from "react";
import Box from "./Box";

function Pattern({ grid }) {
    const onDragStart = (e) => {
        e.dataTransfer.setData("grid", grid);
        e.dataTransfer.setData("cols", grid[0].length);
    };
    return (
        <div
            className="pattern"
            draggable
            onDragStart={onDragStart}
            style={{ width: grid[0].length * 9 }}
        >
            {grid.map((row, i) =>
                row.map((box, j) => (
                    <Box
                        key={`${i}_${j}`}
                        i={i}
                        j={j}
                        alive={box}
                        toggleBox={() => {}}
                        toggleArea={() => {}}
                    />
                ))
            )}
        </div>
    );
}

export default Pattern;
