import React from 'react';
import python from '../../img/logo/python.png';
import css from '../../img/logo/css.png';
import html5 from '../../img/logo/html5.png';
import javascript from '../../img/logo/javascript.png';
import mongodb from '../../img/logo/mongodb.png';
import nodejs from '../../img/logo/nodejs.png';
import numpy from '../../img/logo/numpy.png';
import pandas from '../../img/logo/pandas.png';
import react from '../../img/logo/react.png';
import redux from '../../img/logo/redux.png';
import sass from '../../img/logo/sass.png';

const About = () => {
    return (
        <section id="about" className="about">
            <div className="greeting">Hi, I'm Huabei You</div>
            <div className="console">
                <div className="bar">
                    <div className="dots">
                        <span className="dot red"></span>
                        <span className="dot yellow"></span>
                        <span className="dot green"></span>
                    </div>
                </div>
                <div className="content" style={{ fontSize: '1.1rem' }}>
                    <p>Last login: Sun Aug 2 11:39:08 on console</p>
                    <p>
                        huabei: I am chasing my dream to become a full time web
                        developer.
                    </p>
                    <p>
                        huabei: My goal is to help your company grow with my
                        skill set.
                    </p>
                </div>
                <div className="skills">
                    <div className="skill">
                        <img src={html5} alt="HTML5" />
                        <span>HTML5</span>
                    </div>
                    <div className="skill">
                        <img src={css} alt="CSS3" />
                        <span>CSS5</span>
                    </div>
                    <div className="skill">
                        <img src={javascript} alt="JavaScript" />
                        <span>JavaScript</span>
                    </div>
                    <div className="skill">
                        <img src={sass} alt="Sass" />
                        <span>Sass</span>
                    </div>
                    <div className="skill">
                        <img src={mongodb} alt="MongoDB" />
                        <span>MongoDB</span>
                    </div>
                    <div className="skill">
                        <img src={nodejs} alt="NodeJs" />
                        <span>NodeJS</span>
                    </div>
                    <div className="skill">
                        <img src={react} alt="React" />
                        <span>React</span>
                    </div>
                    <div className="skill">
                        <img src={redux} alt="Redux" />
                        <span>Redux</span>
                    </div>
                    <div className="skill">
                        <img src={python} alt="Python" />
                        <span>Python</span>
                    </div>
                    <div className="skill">
                        <img src={numpy} alt="Numpy" />
                        <span>Numpy</span>
                    </div>
                    <div className="skill">
                        <img src={pandas} alt="pandas" />
                        <span>pandas</span>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default About;
