import React from "react";

const Box = ({ i, j, alive, toggleBox, toggleArea }) => {
    const classStr = alive ? "box on" : "box off";
    const onClick = () => {
        toggleBox(i, j);
    };
    const onDragOver = (e) => {
        e.preventDefault();
    };

    const onDrop = (e) => {
        const grid = e.dataTransfer.getData("grid").split(",");
        const end = grid.length;
        const cols = parseInt(e.dataTransfer.getData("cols"));
        let pattern = [];
        for (let i = 0; i < end; i = i + cols) {
            pattern.push(grid.slice(i, i + cols));
        }
        toggleArea(i, j, pattern);
    };

    return (
        <div
            className={classStr}
            onClick={onClick}
            onDragOver={onDragOver}
            onDrop={onDrop}
        ></div>
    );
};

export default Box;
