import React, { Fragment } from 'react';
import About from './section/About';
import Project from './section/Project';
import Contact from './section/Contact';
import Nav from './layout/Nav';

const Portfolio = () => {
    return (
        <Fragment>
            <Nav />
            <div className="main">
                <About />
                <Project />
                <Contact />
            </div>
        </Fragment>
    );
};

export default Portfolio;
