import React from "react";
import Box from "./Box";

function Board({ rows, cols, grid, setGrid }) {
    const toggleBox = (i, j) => {
        const newGrid = Array.from(grid);
        newGrid[i][j] = !grid[i][j];
        setGrid(newGrid);
    };
    const width = cols * 9;

    const toggleArea = (i, j, pattern) => {
        const draw = pattern.map((array) => {
            return array.map((value) => {
                return value === "true";
            });
        });
        const newGrid = grid.map((array) => array.slice());
        const hLength = draw[0].length;
        const vLength = draw.length;
        let hDistance, vDistance;
        let rowIndex = 0;
        if (hLength % 2 === 1) {
            hDistance = (hLength - 1) / 2;
            if (vLength % 2 === 1) {
                vDistance = (vLength - 1) / 2;
                if (i - vDistance < 0) i = vDistance;
                if (i + vDistance > rows - 1) i = rows - 1 - vDistance;
                if (j - hDistance < 0) j = hDistance;
                if (j + hDistance > cols - 1) j = cols - 1 - hDistance;
                for (
                    let pointer = i - vDistance;
                    pointer < i + vDistance + 1;
                    pointer++
                ) {
                    newGrid[pointer].splice(
                        j - hDistance,
                        hLength,
                        ...draw[rowIndex]
                    );
                    rowIndex++;
                }
            } else {
                vDistance = vLength / 2;
                if (i + 1 - vDistance < 0) i = vDistance - 1;
                if (i + vDistance > rows - 1) i = rows - 1 - vDistance;
                if (j - hDistance < 0) j = hDistance;
                if (j + hDistance > cols - 1) j = cols - 1 - hDistance;
                for (
                    let pointer = i + 1 - vDistance;
                    pointer < i + vDistance + 1;
                    pointer++
                ) {
                    newGrid[pointer].splice(
                        j - hDistance,
                        hLength,
                        ...draw[rowIndex]
                    );
                    rowIndex++;
                }
            }
        } else {
            hDistance = hLength / 2;
            if (vLength % 2 === 1) {
                vDistance = (vLength - 1) / 2;
                if (i - vDistance < 0) i = vDistance;
                if (i + vDistance > rows - 1) i = rows - 1 - vDistance;
                if (j + 1 - hDistance < 0) j = hDistance - 1;
                if (j + hDistance > cols - 1) j = cols - 1 - hDistance;
                let rowIndex = 0;
                for (
                    let pointer = i - vDistance;
                    pointer < i + vDistance + 1;
                    pointer++
                ) {
                    newGrid[pointer].splice(
                        j + 1 - hDistance,
                        hLength,
                        ...draw[rowIndex]
                    );
                    rowIndex++;
                }
            } else {
                vDistance = vLength / 2;
                if (i + 1 - vDistance < 0) i = vDistance - 1;
                if (i + vDistance > rows - 1) i = rows - 1 - vDistance;
                if (j + 1 - hDistance < 0) j = hDistance - 1;
                if (j + hDistance > cols - 1) j = cols - 1 - hDistance;
                for (
                    let pointer = i + 1 - vDistance;
                    pointer < i + vDistance + 1;
                    pointer++
                ) {
                    newGrid[pointer].splice(
                        j + 1 - hDistance,
                        hLength,
                        ...draw[rowIndex]
                    );
                    rowIndex++;
                }
            }
        }
        setGrid(newGrid);
    };
    return (
        <div className="board" style={{ width: width }}>
            {grid.map((row, i) =>
                row.map((box, j) => (
                    <Box
                        key={`${i}_${j}`}
                        i={i}
                        j={j}
                        alive={box}
                        toggleBox={toggleBox}
                        toggleArea={toggleArea}
                    />
                ))
            )}
        </div>
    );
}

export default Board;
