import React from 'react';
import DevConnector from '../../img/devconnector.png';
import Adough from '../../img/adough.png';
import GameOfLife from '../../img/gameoflife.png';
import { Link } from 'react-router-dom';

const Project = () => {
    return (
        <section id="project" className="project">
            <div className="lead">Here are some of my projects:</div>
            <div className="cards">
                <div className="card">
                    <img
                        src={DevConnector}
                        alt="DevConnector"
                        className="thumbnail"
                    />
                    <div className="container">
                        <div
                            className="tag"
                            style={{
                                color: 'rgb(83, 80, 255)',
                                border: '2px solid rgb(83, 80, 255)',
                            }}
                        >
                            Full Stack
                        </div>
                        <div className="name">DevConnector</div>
                        <div className="description">
                            A website for developers to connect with each other.
                            Users can view other developers' profiles and make
                            or comment posts.
                        </div>
                        <div className="link">
                            <a
                                className="btn"
                                href="https://enigmatic-wave-16023.herokuapp.com/"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                <i className="fas fa-chevron-right"></i> View
                                Project
                            </a>
                        </div>
                    </div>
                </div>
                <div className="card">
                    <img src={Adough} alt="Adough" className="thumbnail" />
                    <div className="container">
                        <div
                            className="tag"
                            style={{
                                color: 'rgb(83, 80, 255)',
                                border: '2px solid rgb(83, 80, 255)',
                            }}
                        >
                            Full Stack
                        </div>
                        <div className="name">Adough</div>
                        <div className="description">
                            A website for tracking your diet and health metrics.
                            It also saves diet diary and provides customized
                            dietary goals for logged in users.
                        </div>
                        <div className="link">
                            <a
                                className="btn"
                                href="https://sleepy-springs-12957.herokuapp.com/"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                <i className="fas fa-chevron-right"></i> View
                                Project
                            </a>
                        </div>
                    </div>
                </div>
                <div className="card">
                    <img
                        src={GameOfLife}
                        alt="Game of Life"
                        className="thumbnail"
                    />
                    <div className="container">
                        <div
                            className="tag"
                            style={{
                                color: '#008088',
                                border: '2px solid #008088',
                            }}
                        >
                            Front End
                        </div>
                        <div className="name">Game of Life</div>
                        <div className="description">
                            A cellular automaton game devised by the British
                            mathematician John Horton Conway in 1970.
                            Implemented with React.
                        </div>
                        <div className="link">
                            <Link className="btn" to="/project/gol">
                                <i className="fas fa-chevron-right"></i> Play
                                Now
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Project;
